@import '../../../../node_modules/swiper/css/swiper.min.css';

.swiper-container-new {
  width: 100%;
  padding-top: 50px;
  padding-bottom: 66px;
  overflow: hidden;
  margin-bottom: 20px; }

.swiper-title {
  font: 800 33px 'Roboto',sans-serif; }

.swiper-slide {
  background-position: center;
  background-size: cover;
  width: 300px;
  height: 300px;

  a {
    position: absolute;
    height: 100%;

    span {
      height: 20%;
      width: 75%;
      background-color: rgba(10, 10, 10, 0.5);
      color: #fff;
      display: flex;
      align-items: center;
      padding: 0px 36px; } } }
